:host { }

.fle-hero-banner {
  display: block;
  position: relative;
  width: 100%;
  min-height: 30rem;

  .media {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .info {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    //width: 90%;
    //max-width: 90%;

    @media (min-width: map-get($grid-breakpoints, xl)) {
      width: 30%;
      max-width: 550px;

      &.left {
        right: unset;
        left: 10%;
      }

      &.right {
        right: 10%;
        left: unset;
      }
    }
  }
}
