import { Component, h, Host, Prop } from '@stencil/core';
import { HorizontalAlignment } from '../../utils/components.model';

@Component({
  tag: 'fle-hero-banner',
  styleUrl: 'fle-hero-banner.scss',
  shadow: false, // no shadow dom cause CMSs system need access for live preview
})
export class FleHeroBanner {
  @Prop() headline: string;
  @Prop() category: string;
  @Prop() description: string;
  @Prop() mediaUrl: string;
  @Prop() mediaAltText: string;
  @Prop() infoPosition: HorizontalAlignment = 'left';
  @Prop() buttonUrls: string[];

  render() {
    const infoAlignmentClass = this.infoPosition === 'left' ? 'left' : 'right';
    return (
      <Host class="fle-hero-banner">
        <fle-media class="media" media-url={this.mediaUrl} media-alt-text={this.mediaAltText}></fle-media>
        <fle-info class={`info ${infoAlignmentClass}`} headline={this.headline} category={this.category} description={this.description}></fle-info>
        {/*<slot></slot>*/}
      </Host>
    );
  }
}
